import styled from "styled-components";

export const Header = styled.div`
	${props => props.theme.fonts.GothamMedium32};
	color: ${props => props.theme.colors.white.white100};
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-left: 20rem;
`

export const Wrapper = styled.div`

`

export const Filters = styled.div`
	display: grid;
	gap: 3rem;
	grid-template-columns: 1fr 1fr;
`

export const Table = styled.div`
	margin-top: 2rem;
	width: 100%;
	padding: 3rem;
	border: .3rem solid ${props => props.theme.stroke.stroke5};
	${props => props.theme.shadows.defaultShadow};
	border-radius: 2.5rem;
`

export const Head = styled.div`
	display: grid;
	grid-template-columns: .1fr .2fr .3fr minmax(20rem, .55fr) .45fr .3fr .2fr;
	column-gap: 2rem;
`

export const HeadItem = styled.span`
	${props => props.theme.fonts.GothamBold20};
	color: ${props => props.theme.colors.white.white100};
	column-gap: 3rem;
`

export const Body = styled.div`
	margin-top: 3rem;
	display: grid;
	row-gap: 3rem;
	grid-template-columns: .1fr .2fr .3fr minmax(20rem, .55fr) .45fr .3fr .2fr;
	column-gap: 2rem;	
`

export const BodyItem = styled.div`
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
`

export const Server = styled(BodyItem)`
	${props => props.theme.mixins.ellipsis};
`

export const Duration = styled(BodyItem)`

`

export const Reason = styled(BodyItem)`
	color: ${props => props.theme.colors.red.accent};
`