import { IFriend } from './../../interfaces/Friend';
import { IUser } from '../../interfaces/User';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import API from 'API';

interface AuthState {
	isAuth: boolean;
	user: IUser;
	anotherUser: IUser;
	friends: IFriend[];
	personalFriends: IFriend[];
}

const initialState: AuthState = {
	isAuth: false,
	user: {} as IUser,
	anotherUser: {} as IUser,
	friends: [],
	personalFriends: [],
}

export const login = createAsyncThunk(
	'user/login',
	async () => {
		const response = await API.UserService.getProfile()
		return response
	}
)

export const fetchFriends = createAsyncThunk(
	'user/fetchFriends',
	async (steamid64: string) => {
		const response = await API.UserService.getFriends(steamid64)
		return response.data
	}
)

export const userSlice = createSlice({
	name: 'user',
	initialState,
	reducers: {
		setAnotherUser: (state, action) => {
			state.anotherUser = action.payload
		},
		setUser: (state, action) => {
			state.user = action.payload
		}
	},
	extraReducers: (builder) => {
		builder.addCase(login.fulfilled, (state, action) => {
			if (action.payload.success) {
				state.isAuth = true
				state.user = action.payload.data
				localStorage.setItem('your_steamid64', action.payload.data.user.steamid64)
			}
		})
		builder.addCase(fetchFriends.fulfilled, (state, action) => {
			if (action.payload) {
				state.friends = action.payload || []
				state.personalFriends = action.payload || []
			}
		})
	}
})

export const { setAnotherUser, setUser } = userSlice.actions
export const userActions = userSlice.actions
export default userSlice.reducer