import { SelectVariants } from 'interfaces/Select';
import React from 'react'
import { BodyBlock, Item } from './Styles'

interface Props {
	show: boolean;
	items: any[];
	onItemClick: ({ index }: any) => any;
	variant?: SelectVariants;
}

export default function Body({ show, items, onItemClick, variant }: Props) {
	return (
		<BodyBlock className={show ? 'customScroll active' : 'customScroll'} variant={variant}>
			{items.map((item, index) =>
				<Item
					key={index}
					onClick={() => onItemClick(index)}>
					{item?.label ? item?.label : item}
				</Item>
			)}
		</BodyBlock>
	)
}
