import { useEffect, useState } from 'react'
import Chat from "components/Chat";
import React from "react";
import Header from "../Header";
import { ChatWrapper, Container, StyledLayout, ToggleChat } from "./Styles";
import { IMessage } from 'interfaces/Message';
import { io } from 'socket.io-client';
import { toast } from 'react-toastify';
import API, { url } from 'API';
import Toast from 'components/UI/Toast';

export const chat = io(url, { withCredentials: true })
interface Props {
	children: any;
	headerElement?: React.ReactElement | string;
	footerElement?: React.ReactElement | string;
	leftElement?: React.ReactElement | string
}

export default function Layout({ children, headerElement, footerElement, leftElement }: Props) {

	const [chatItems, setChatItems] = useState<IMessage[]>([])

	useEffect(() => {
		(async () => {
			const response = await API.ChatService.getHistory()
			setChatItems(response)
		})()
		chat.on('recMessage', (data) => setChatItems(prev => [...prev, data]))
		chat.on('error', (error) => toast.error(error.message))
	}, [])

	const [isChatActive, setChatActive] = useState(false)

	return (
		<StyledLayout>
			<Header>
				{headerElement}
			</Header>
			<Container isChatActive={isChatActive}>
				{leftElement}
				{children}
				<ChatWrapper isChatActive={isChatActive}>
					<Chat isActive={isChatActive} items={chatItems} />
					<ToggleChat onClick={() => setChatActive(!isChatActive)}>
						<button className={`dots ${isChatActive ? 'on' : ''}`}>
							<span />
						</button>
					</ToggleChat>
				</ChatWrapper>
			</Container>
			{footerElement}
			<Toast params={{ autoClose: 8000, limit: 2 }} />
		</StyledLayout>
	)
}
