import { createGlobalStyle } from "styled-components";

export const GlobalStyles = createGlobalStyle`
	*,
  *::after,
  *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
		color: white;
  }

  html, body {
    font-size: 10px;
    font-family: 'Gotham Pro', 'sans-serif';
    overflow-x: hidden;
    scroll-behavior: smooth;
  }

	button {
    border: none;
    outline: none;
    cursor: pointer;
  }

	img, svg {
		user-select: none;
	}

  input {
    border: none;
    outline: none;
  }

  a {
    text-decoration: none;
  }

	.App {
		width: 100vw;
		height: 100vh;
		overflow-x: hidden;
		background: ${props => props.theme.gradients.bg};
		display: flex;
		position: relative;
	}

	.hideScroll {
		-ms-overflow-style: none;
		scrollbar-width: none;
		&::-webkit-scrollbar {
			display: none;
		}
	}

	.customScroll, .epr-body {
		overflow-y: auto;
		overflow-x: hidden;
		scrollbar-width: thin;
		&::-webkit-scrollbar {
			width: .4rem;
			border: .1rem solid transparent;
		}
		&::-webkit-scrollbar-thumb {
			background: ${props => props.theme.colors.blue.accent} !important;
			border-radius: .5rem !important;
		}
		&::-webkit-scrollbar-track {
			background: ${props => props.theme.colors.white.white10} !important;
		}
	}

	.toast-body {
		${props => props.theme.fonts.GothamChatRegular12};
	}
`