import { IDropDown } from "interfaces/DropDown";
import { Wrapper } from "./Styles";
import Head from "./Head";
import Body from "./Body";
import { useState } from "react";

export default function DropDown({ headParams, items, onChange, defaultValue = '' }: IDropDown) {

	const [show, setShow] = useState<boolean>(false)
	const [headLabel, setHeadLabel] = useState<string>('')
	const [defVal, setDefVal] = useState<string>(defaultValue)

	function onItemClick(index: number) {
		setShow(false)
		
		const label = items[index]?.label ? items[index]?.label : items[index]
		const value = items[index]?.value ? items[index]?.value : items[index]

		setHeadLabel(label)
		setDefVal('')
		onChange(value)
	}

	return (
		<Wrapper>
			<Head
				setBodyShow={setShow}
				show={show}
				params={headParams}
				label={headLabel}
				defaultValue={defVal}
			/>
			<Body
				show={show}
				items={items}
				onItemClick={onItemClick}
			/>
		</Wrapper>
	)
}
