import Row from 'components/Row'
import Inventoryfilters from 'components/Templates/Inventoryfilters'
import UIKIT from 'components/UI'
import React, { useState } from 'react'
import { Transfer } from './Styles'

export default function Header() {

	const [isTransfer, setTransfer] = useState(false)

	return (
		<Row>
			<Inventoryfilters />
			<UIKIT.Button variant='default' color='blue' height='4.5rem' onClick={() => setTransfer(!isTransfer)}>
				Перенос предметов
			</UIKIT.Button>
			<Transfer className={isTransfer ? 'active' : ''}>
				<UIKIT.Button variant='default' color='blue' height='5rem'>
					С сайта на сервер
				</UIKIT.Button>
				<UIKIT.Button variant='default' color='blue' height='5rem'>
					С сервера на сайт
				</UIKIT.Button>
			</Transfer>
		</Row>
	)
}
