import Image from 'components/Image'
import LabelAvatar from 'components/Labels/LabelAvatar'
import { IWeaponCard } from 'interfaces/WeaponCard'
import { useTheme } from 'styled-components'
import { Container, Info, Name, Price, SubName } from './Styles'

export default function WeaponCard({ src, name, sub_name, price, owner, rarity, ...props }: IWeaponCard) {

	const Theme = useTheme()

	return (
		<Container rarity={rarity} {...props}>
			<Image src={src} alt='Gun' />
			<Info>
				<Name>{name} | <SubName rarity={rarity}>&nbsp;{sub_name}</SubName></Name>
				<Price>{price} Kp</Price>
				<LabelAvatar params={{ font: Theme.fonts.GothamRegular14, avatarSize: 'super-small' }}>
					{owner?.nickname}
				</LabelAvatar>
			</Info>
		</Container>
	)
}
