import React, { useEffect, useState, useRef } from 'react'
import { useTheme } from "styled-components";
import { Buttons, ChatButton, Container, EmojiWrapper, InputBlock, Messages } from "./Styles";
import { useAnimation } from 'framer-motion'
import ChatItem from './ChatItem';
import { IMessage } from 'interfaces/Message';
import Input from 'components/UI/Input';
import { chat } from 'components/Layout/Layout';
import { TinyIcons } from 'assets/tiny-icons';
import { useAppSelector } from 'hooks/useAppSelector';
import { SuperTinyIcons } from 'assets/super-tiny-icons';
import EmojiPicker, { Theme as EmojiTheme } from 'emoji-picker-react';
import { EmojiClickData } from 'emoji-picker-react/dist/types/exposedTypes';

interface Props {
	isActive: boolean;
	items: IMessage[];
}

export default function Chat({ isActive, items }: Props) {

	const Theme = useTheme()
	const control = useAnimation()
	const [message, setMessage] = useState('')
	const { user } = useAppSelector(state => state.user?.user)
	const [showEmoji, setShowEmoji] = useState<boolean>(false)
	const chatRef = useRef<HTMLDivElement>(null)

	useEffect(() => {
		control.start(isActive ? 'show' : 'hide')
	}, [isActive])

	useEffect(() => {
		if(chatRef.current) {
			chatRef.current.scrollTo(0, 999999)
		}
	}, [items])

	function sendMessage(message: { nickname: string, text: string }) {
		chat.emit('sendMessage', message)
	}

	function submitHandler(event: React.FormEvent<HTMLFormElement>) {
		message && sendMessage({ nickname: user.name, text: message })
		setMessage('')
		event.preventDefault()
	}

	function emojiButtonClickHandler(event: React.MouseEvent<HTMLButtonElement>) {
		event.preventDefault()
		setShowEmoji(!showEmoji)
	}

	function emojiPickHandler(props: EmojiClickData) {
		setMessage(prev => prev + props.emoji)
	}

	function onMessageChange(event: React.ChangeEvent<HTMLInputElement>) {
		setMessage(event.target.value)
	}

	return (
		<Container
			variants={Theme.motion.openChat}
			initial="start"
			animate={control}
			transition={Theme.motion.chat}
			className={isActive ? 'active' : 'hidden'}
			onSubmit={submitHandler}
		>
			<Messages className='customScroll' ref={chatRef}>
				{items.map(({ nickname, id, text, imagePath, createdAt, steamid64, avatar }) =>
					<ChatItem
						steamid64={steamid64}
						avatar={avatar}
						key={id}
						nickname={nickname}
						text={text}
						imagePath={imagePath}
						createdAt={createdAt}
						id={id}
					/>
				)}
			</Messages>
			<InputBlock>
				<Input
					onChange={onMessageChange}
					value={message}
					placeholder='Сообщение...'
					params={{ width: '100%', font: Theme.fonts.GothamRegular16 }}
				/>
				{showEmoji &&
					<EmojiWrapper>
						<EmojiPicker
							theme={EmojiTheme.DARK}
							height={510}
							width={336}
							onEmojiClick={emojiPickHandler}
						/>
					</EmojiWrapper>
				}
				<Buttons>
					<ChatButton variant='gray' onClick={emojiButtonClickHandler} type='button'>
						<TinyIcons.Emoji />
					</ChatButton>
					<ChatButton variant='blue'>
						<SuperTinyIcons.ArrowLeft />
					</ChatButton>
				</Buttons>
			</InputBlock>
		</Container>
	)
}
