import Avatar from 'components/Avatar'
import { Container, Nickname, OnlineStatus, ProfileData, ServerOnline, SiteOnline, Wrapper } from './Styles'
import UIKIT from 'components/UI'
import { IUser } from 'interfaces/User'

export function ProfileCard({ user }: { user: IUser }) {

	return (
		<Container>
			<Avatar size='big' src={user?.user?.avatar} />
			<Wrapper>
				<ProfileData>
					<Nickname>
						{user?.user?.name}
					</Nickname>
					<OnlineStatus>
						<SiteOnline online={user?.online}>
							{user?.online ? 'Онлайн' : 'Не в сети'}
						</SiteOnline> |
						<ServerOnline>&nbsp;
							Не в сети
						</ServerOnline>
					</OnlineStatus>
				</ProfileData>
				<UIKIT.Progress
					exp={user?.level?.currentEXP}
					nextExp={1500}
					level={user?.level?.currentLevel}
					nextLevel={user?.level?.currentLevel + 1}
				/>
			</Wrapper>
		</Container>
	)
}
