import React, { useState } from 'react'
import { SuperTinyIcons } from 'assets/super-tiny-icons'
import Avatar from 'components/Avatar'
import { parseChatTime } from 'helpers/parseChatTime'
import { IMessage } from 'interfaces/Message'
import { isYou } from 'utils/IsYou'
import { Container, Message, MessageWrapper, Nickname, Text, Timestamp, Wrapper } from './Styles'

export default function ChatItem({ nickname, imagePath, text, id, createdAt, steamid64, avatar }: IMessage) {

	const ISYOU = isYou(steamid64)
	const [isMenu, setIsMenu] = useState<boolean>(false)

	function setMenu() {
		setIsMenu(!isMenu)
	}

	return (
		<Container ISYOU={ISYOU}>
			<Wrapper>
				{ISYOU ?
					<>
						<Nickname ISYOU={ISYOU} to={`/profile/${steamid64}`}>{nickname}</Nickname>
						<MessageWrapper isMenu={isMenu}>
							<SuperTinyIcons.MessageDots onClick={setMenu} />
							<Message ISYOU={ISYOU}>
								<Text>{text}</Text>
								<Timestamp>
									{parseChatTime(new Date(createdAt))}
								</Timestamp>
							</Message>
						</MessageWrapper>
					</> :
					<>
						<MessageWrapper isMenu={isMenu}>
							<SuperTinyIcons.MessageDots onClick={setMenu} />
							<Message ISYOU={ISYOU}>
								<Text>{text}</Text>
								<Timestamp>
									{parseChatTime(new Date(createdAt))}
								</Timestamp>
							</Message>
						</MessageWrapper>
						<Nickname ISYOU={ISYOU} to={`/profile/${steamid64}`}>{nickname}</Nickname>
					</>
				}
			</Wrapper>
			<Avatar size='super-small' alt='avatar' src={avatar} />
		</Container>

	)
}
