import Layout from 'components/Layout'
import ProfileLayout from 'components/ProfileLayout'
import { useAppSelector } from 'hooks/useAppSelector'
import React from 'react'
import AvatarChange from './Avatar'
import Blacklist from './Backlist'
import Ref from './Ref'
import { Grid, Left, Right } from './Styles'
import VK from './VK'
import { useForm } from 'react-hook-form'
import { ProfileSettingsContext } from 'context/ProfileSettings'
import Profile from './Profile'
import AdditionalSettings from './AdditionalSettings'

export interface IProfileSettings {
	name: string;
	server_nickname: string;
	birthday: {
		day: number;
		month: string;
		year: number;
	}
	email: string;
	discord: string;
}

export default function Settings() {

	const { user } = useAppSelector(state => state.user?.user)

	const { control } = useForm<IProfileSettings>({
		defaultValues: {
			name: 'BaDick',
			server_nickname: user?.name || '',
			birthday: {
				day: 11,
				month: 'december',
				year: 2002,
			},
			email: 'mail@gmail.com',
			discord: '#6666sosibibu'
		}
	})

	return (
		<ProfileSettingsContext.Provider value={{ control }}>
			<Layout
				leftElement={<div />}
				headerElement={<div />}
			>
				<ProfileLayout>
					<Grid>
						<Left>
							<AvatarChange />
							<VK />
							<Ref />
							<Blacklist />
						</Left>
						<Right>
							<Profile control={control} />
							<AdditionalSettings />
						</Right>
					</Grid>
				</ProfileLayout>
			</Layout>
		</ProfileSettingsContext.Provider>
	)
}
