import { LazyImage } from './Styles';

interface ImageProps {
	key?: string;
	src: string;
	alt: string;
	height?: string;
	width?: string;
	caption?: string;
	className?: string
}

export default function Image({ key, src, alt, width, height, className, ...props }: ImageProps) {
	return (
		<LazyImage
			key={key}
			alt={alt}
			height={height}
			src={src}
			width={width}
			className={className}
			{...props}
		/>
	)
}
