import Image from 'components/Image'
import { IInventoryWeaponCard } from 'interfaces/InventoryWeaponCard'
import React from 'react'
import { Container, Info, Name, SubName } from './Styles'

export function InventoryWeaponCard({ name, rarity, src, subname, ...props }: IInventoryWeaponCard) {
	return (
		<Container rarity={rarity} {...props}>
			<Image src={src} alt='Gun' />
			<Info>
				<Name>{name} | <SubName rarity={rarity}>&nbsp;{subname}</SubName></Name>
			</Info>
		</Container>
	)
}
