import { Card, Container, Map, Online, Type } from "./Styles";
import { IServer } from 'interfaces/Server'
import Image from "components/Image";
import { TinyIcons } from "assets/tiny-icons";
import ActionButton from "components/UI/ActionButton";
import ServerPlayers from "components/ServerPlayers";

export function ServerCard({ map, max_players, players, type, ...props }: IServer) {
	return (
		<Card {...props}>
			<div className="bg" />
			<Image
				src="/static/images/map.png"
				alt="server"
			/>
			<Container>
				<Map onClick={e => e.stopPropagation()}>{map}</Map>
				<Type onClick={e => e.stopPropagation()}>
					{type}
					<ActionButton>
						<TinyIcons.Play />
					</ActionButton>
					<ActionButton>
						<TinyIcons.Copy />
					</ActionButton>
				</Type>
				<Online onClick={e => e.stopPropagation()}>
					<ServerPlayers players={players || 0} max_players={max_players || 0} />
				</Online>
			</Container>
		</Card>
	)
}
