import { IInput } from 'interfaces/Input'
import React from 'react'
import { InputBlock, Label, Text } from './Styles'

export default function Input({ mask, maskChar, label, params, placeholder, ...props }: IInput) {
	return (
		<Label width={params?.width} color={params?.labelColor}>
			<Text params={params}>{label}</Text>
			<InputBlock
				mask={mask}
				maskChar={maskChar}
				placeholder={placeholder}
				params={params}
				{...props}
			/>
		</Label>
	)
}
