import Row from 'components/Row'
import React from 'react'
import { Text } from './Styles';

interface Props {
	icon: React.ReactElement | string;
	children: any;
	params?: {
		font?: string;
		color?: string;
		gap?: string;
	}
}

export default function LabelIcon({ icon, children, params }: Props) {
	return (
		<Row gap={params?.gap || '1rem'}>
			{icon}
			<Text color={params?.color} font={params?.font}>
				{children}
			</Text>
		</Row>
	)
}
