import { InventoryWeaponCard } from 'components/Cards'
import Layout from 'components/Layout'
import ProfileLayout from 'components/ProfileLayout'
import { IInventoryWeaponCard } from 'interfaces/InventoryWeaponCard'
import React from 'react'
import Header from './Header'
import { Grid, Wrapper } from './Styles'

export default function Inventory() {

	const items: IInventoryWeaponCard[] = [
		{
			rarity: 'classified',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'common_grade',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'covert',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'industrial_grade',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'mil_spec',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'restricted',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'exceedingly_rare',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'exceedingly_rare',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'exceedingly_rare',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
		{
			rarity: 'exceedingly_rare',
			name: 'USP-S',
			subname: 'Gavno',
			src: '/static/images/Gun.png'
		},
	]

	return (
		<Layout
			leftElement={<div />}
			headerElement={<div />}
		>
			<ProfileLayout gap='3rem'>
				<Wrapper>
					<Header />
				</Wrapper>
				<Grid>
					{items.map((item, index) => 
						<InventoryWeaponCard 
							key={index}
							src={item.src}
							rarity={item.rarity}
							name={item.name}
							subname={item.subname}
						/>	
					)}
				</Grid>
			</ProfileLayout>
		</Layout>
	)
}
