import React from 'react'
import { ToastContainer, ToastContainerProps } from 'react-toastify'

interface Props {
	params?: ToastContainerProps
}

export default function Toast({ params }: Props) {
	return (
		<ToastContainer
			theme='dark'
			position="bottom-right"
			bodyClassName='toast-body'
			{...params}
		/>
	)
}
