import Layout from 'components/Layout'
import ProfileLayout from 'components/ProfileLayout'
import Information from 'components/Templates/Information'
import InformationColumn from 'components/Templates/Information/Column'
import { useAppSelector } from 'hooks/useAppSelector'
import React from 'react'
import { Body, BodyItem, Head, HeadItem, History, Row, ServiceItem, Wrapper } from './Styles'

export default function Stats() {

	const { statistics } = useAppSelector(state => state.user.user)

	return (
		<Layout
			leftElement={<div />}
			headerElement={<div />}
		>
			<ProfileLayout gap='3rem'>
				<Wrapper>
					<Information>
						<InformationColumn items={['Убийств:', statistics?.kills]} />
						<InformationColumn items={['Смертей:', statistics?.deaths]} />
						<InformationColumn items={['K/D:', (statistics?.kills / statistics?.deaths).toFixed(2)]} />
						<InformationColumn items={['Наиграно:', statistics?.timePlayed + ' ч']} />
					</Information>
					<History>
						<Head>
							<Row>
								<HeadItem>№</HeadItem>
								<HeadItem>Сервер</HeadItem>
								<HeadItem>Идентификатор</HeadItem>
								<HeadItem>Услуги</HeadItem>
							</Row>
						</Head>
						<Body>
							<Row>
								<BodyItem>1</BodyItem>
								<BodyItem>Freezing-Blood | MIRAGE #1</BodyItem>
								<BodyItem>STEAM_1:0:566407427</BodyItem>
								<ServiceItem>Совладлец MIRAGE #1</ServiceItem>
							</Row>
							<Row>
								<BodyItem>1</BodyItem>
								<BodyItem>Freezing-Blood | MIRAGE #1</BodyItem>
								<BodyItem>STEAM_1:0:566407427</BodyItem>
								<ServiceItem>Совладлец MIRAGE #1</ServiceItem>
							</Row>
						</Body>
					</History>
				</Wrapper>
			</ProfileLayout>
		</Layout>
	)
}
