import { Container as ColumnContainer } from './../Column/Styles';
import styled from "styled-components";

interface ContainerProps {
	isChatActive?: boolean
}

export const StyledLayout = styled.main`
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	margin-left: calc(${props => props.theme.consts.navBarWidth} + 4rem);
	padding-bottom: 5rem;
`

export const Wrapper = styled.div`
	
`

export const Container = styled.div<ContainerProps>`
	transition: .5s;
	width: 100%;
	margin-bottom: 3rem;
	height: fit-content;
	position: relative;
	flex: 1;	
	display: grid;
	grid-template-columns: 3.4rem 1fr ${props => props.isChatActive ? "43.4rem" : "3.4rem"};
	gap: 2rem;
	padding-right: 4.6rem;
  ${ColumnContainer} {
    align-self: flex-start;
  }
`

export const ChatWrapper = styled.div<ContainerProps>`
	display: flex;
	position: relative;
	gap: ${props => props.isChatActive ? "1.6rem" : "0rem"};
	transition: .5s;
`

export const ToggleChat = styled.div`
	z-index: 20;
	${props => props.theme.mixins.dfcenter};
	border-radius: 2rem;
	flex-direction: column;
	gap: .4rem;
	cursor: pointer;
	width: 3.4rem;
	height: 18rem;
	border: .3rem solid ${props => props.theme.stroke.stroke5};
	${props => props.theme.shadows.defaultShadow};
	&:last-child {
		position: absolute;
		right: 0;
	}
	button.dots {
  width: 60px;
  height: 30px;
  border: none;
  background: transparent;
  position: relative;
  cursor: pointer;
  &:focus {
    outline:none;
  }
  
  &.dots {
    &:after,
    &:before,
    span {
      width: 10%;
      height: 20%;
      border-radius: 100px;
      position: absolute;
      left: 0px;
      background: ${props => props.theme.colors.white.white100};
      transform: rotate(0deg);
      transition: all 0.5s;
      left: 1%;
      right: 1%;
      margin: 0 auto;

    };

    &:after,
    &:before {
      content:"";
    };

    &:after {
      top:-10%;
      margin-top: 0px;
      
    };
    &:before {
      bottom: -10%;
      margin-bottom:0px;
    };

    span {
      top: 50%;
      margin-top: -3px;
    };

    &.on {
			span {
				opacity: 0;
			}

      &:after {
        transform:rotate(135deg) translate(11px, -11px);
        width: 30%;
				height: 7%;
      };

      &:before { 
        transform: rotate(225deg);
        bottom: 50%;
        width: 31%;
				height: 7%;
      };

      span {
        transform:rotate(135deg);
      };

    };
  };
}
`