import Layout from 'components/Layout'
import Tabs from 'components/Templates/Tabs'
import { StoreContext } from 'context/store'
import { useAppDispatch } from 'hooks/useAppDispatch'
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { fetchShop } from 'redux/reducers/shop.slice'
import Credits from './Credits'
import Privileges from './Privileges'

export default function Store() {

	const params = useParams()

	const [tab, setTab] = useState(0)

	const dispatch = useAppDispatch()

	useEffect(() => {
		dispatch(fetchShop())
	}, [])

	return (
		<StoreContext.Provider value={{ tab, setTab }}>
			<Layout
				leftElement={<div />}
				headerElement={
					<Tabs
						context={StoreContext}
						type='link'
						items={[
							{ label: 'Привилегии', href: '/store' },
							{ label: 'Кредиты', href: '/store/credits' },
						]}
					/>
				}
			>
				{params.tab === undefined && <Privileges />}
				{params.tab === 'credits' && <Credits />}
			</Layout>
		</StoreContext.Provider>
	)
}
