import { TinyIcons } from 'assets/tiny-icons'
import { SelectVariants } from 'interfaces/Select';
import React from 'react'
import { HeadBlock, Text } from './Styles'

interface Props {
	position?: 'left' | 'center' | 'right';
	label?: string;
	setShow: React.SetStateAction<any>;
	show: boolean;
	defaultValue?: string;
	height?: string;
	variant?: SelectVariants
}

export default function Head({ setShow, show, defaultValue, label, height, position, variant }: Props) {
	return (
		<HeadBlock
			onClick={() => setShow(!show)}
			className={show ? 'active' : ''}
			height={height}
			position={position}
			variant={variant}
		>
			{defaultValue ?
				<Text>
					{defaultValue}
				</Text> :
				<Text>
					{label ? label : 'Не выбрано'}
				</Text>
			}
			<TinyIcons.Arrow />
		</HeadBlock>
	)
}
