import { Button } from 'components/UI/Button/Styles';
import { Label } from 'components/UI/Input/Styles';
import styled from "styled-components"

interface Props {
	rarity: string;
}

export const Container = styled.div`
	width: 43.4rem;
	height: fit-content;
	padding: 1.5rem 3rem 3rem;
	border-radius: 2rem;
	${props => props.theme.shadows.defaultShadow};
	background: ${props => props.theme.colors.white.white100};
	display: flex;
	flex-direction: column;
	gap: 1rem;
`

export const Bottom = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 2rem;
	${Button}, ${Label} {
		width: 100%;
		max-width: 100%;
	}
`

export const Info = styled.div`
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
	align-items: center;
	justify-content: center;
`

export const Name = styled.div`
	${props => props.theme.fonts.GothamMedium32};
	color: ${props => props.theme.colors.black.black100};
	display: flex;
	font-weight: 400;
`

export const SubName = styled.div<Props>`
	font-weight: bold;
	${props => props.theme.fonts.GothamBold32};
	color: ${props => props.theme.colors.weapon[props.rarity]};
`

export const Price = styled.div`
	${props => props.theme.fonts.GothamBold24};
	color: ${props => props.theme.colors.black.black100};	
`

export const BuyMode = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
`

export const SellMode = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
`

export const OnSellMode = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
`