import { useState } from 'react'
import Layout from "components/Layout";
import Filters from "components/Templates/Filters";
import WeaponCard from 'components/Cards/WeaponCard';
import { Weapons } from './Styles';
import { MarketplaceContext } from 'context/marketplace';
import { Modals } from 'components/Modal';
import { IWeaponCard } from 'interfaces/WeaponCard';
import Column from 'components/Column';
import LayoutIcon from 'components/LayoutIcon';
import UIKIT from 'components/UI'
import { Icons } from 'assets/icons';
import { TinyIcons } from 'assets/tiny-icons';
import Row from 'components/Row';

export default function Marketplace() {

	const [modal, setModal] = useState(false)
	const [mode, setMode] = useState(0)
	const [currentWeapon, setCurrentWeapon] = useState<IWeaponCard>({ src: '', name: '', sub_name: '', rarity: 'common_grade', price: '' })

	const data: IWeaponCard[] = [
		{
			src: '/static/images/Gun.png',
			name: 'Убийца',
			sub_name: 'Крови',
			price: '2007',
			rarity: 'exceedingly_rare',
			owner: {
				src: '',
				nickname: 'VALERA'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: 'Skin4ik',
			sub_name: 'Valery',
			price: '666',
			rarity: 'covert',
			owner: {
				src: '',
				nickname: 'Den4ik_1316'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: 'USP-S',
			sub_name: 'Cortex',
			price: '900.81',
			rarity: 'exceedingly_rare',
			owner: {
				src: '',
				nickname: 'VALERA'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: 'USP-S',
			sub_name: 'Cortex',
			price: '453.81',
			rarity: 'covert',
			owner: {
				src: '',
				nickname: 'Den4ik_1316'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: 'USP-S',
			sub_name: 'Cortex',
			price: '7675.81',
			rarity: 'exceedingly_rare',
			owner: {
				src: '',
				nickname: 'VALERA'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: '324-S',
			sub_name: 'Cortex',
			price: '111.81',
			rarity: 'covert',
			owner: {
				src: '',
				nickname: 'Den4ik_1316'
			}
		},
		{
			src: '/static/images/Gun.png',
			name: 'USP-S',
			sub_name: 'Cortex',
			price: '111.81',
			rarity: 'exceedingly_rare',
			owner: {
				src: '',
				nickname: 'VALERA'
			}
		},
	]

	const [_, setFoo] = useState(null)

	return (
		<MarketplaceContext.Provider value={currentWeapon}>
			<Layout
				headerElement={
					<Filters>
						<UIKIT.DropDown
							onChange={setFoo}
							items={['first', 'second', 'dthghfghfhf']}
							headParams={{
								title: 'Класс:',
								icon: <TinyIcons.Arrow />
							}}
						/>
						<UIKIT.DropDown
							onChange={setFoo}
							items={['first', 'second', 'dthghfghfhf']}
							headParams={{
								title: 'Класс:',
								icon: <TinyIcons.Arrow />
							}}
						/>
						<Row gap='3rem'>
							<UIKIT.DropDown
								onChange={setFoo}
								items={['first', 'second', 'dthghfghfhf']}
								headParams={{
									title: 'Класс:',
									icon: <TinyIcons.Arrow />
								}}
							/>
							<UIKIT.DropDown
								onChange={setFoo}
								items={['first', 'second', 'dthghfghfhf']}
								headParams={{
									title: 'Класс:',
									icon: <TinyIcons.Arrow />
								}}
							/>
						</Row>
					</Filters>
				}
				leftElement={
					<Column gap='2rem'>
						<LayoutIcon className={mode === 0 ? 'active' : ''} onClick={() => setMode(0)}>
							<Icons.Buy />
						</LayoutIcon>
						<LayoutIcon className={mode === 1 ? 'active' : ''} onClick={() => setMode(1)}>
							<Icons.Sell />
						</LayoutIcon>
						<LayoutIcon className={mode === 2 ? 'active' : ''} onClick={() => setMode(2)}>
							<Icons.OnSale />
						</LayoutIcon>
					</Column>
				}
			>
				<Weapons>
					{data.map((item, index) =>
						<WeaponCard
							key={index}
							src={item.src}
							name={item.name}
							sub_name={item.sub_name}
							price={item.price}
							rarity={item.rarity}
							owner={item.owner}
							onClick={() => {
								setCurrentWeapon(data[index])
								setModal(true)
							}}
						/>
					)}
				</Weapons>
				<Modals.MarketplaceWeapon isActive={modal} setActive={setModal} mode={mode} />
			</Layout>
		</MarketplaceContext.Provider>
	)
}
