import { endpoints, url } from "API";
import axios from "axios";

export class UserService {
	static async getProfile() {
		const response = await axios.get(url + endpoints.user.profile, { withCredentials: true })
		return response.data
	}
	static async getAnotherProfile(steamid64) {
		const response = await axios(url + endpoints.user.profile + `/${steamid64}`, { withCredentials: true })
		return response.data
	}
	static async getFriends(steamid64) {
		const response = await axios.get(url + `/profile/${steamid64}` + endpoints.user.friends, { withCredentials: true })
		return response.data
	}
}