import { TinyIcons } from 'assets/tiny-icons'
import Row from 'components/Row'
import UIKIT from 'components/UI'
import React, { useState } from 'react'
import Filters from '../Filters'

export default function InventoryFilters() {

	const [_, setFoo] = useState(null)

	return (
		<Filters params={{ width: '100%' }}>
			<UIKIT.DropDown
				onChange={setFoo}
				items={['first', 'second', 'dthghfghfhf']}
				headParams={{
					title: 'Класс:',
					icon: <TinyIcons.Arrow />
				}}
			/>
			<UIKIT.DropDown
				onChange={setFoo}
				items={['first', 'second', 'dthghfghfhf']}
				headParams={{
					title: 'Тип:',
					icon: <TinyIcons.Arrow />
				}}
			/>
			<UIKIT.DropDown
				onChange={setFoo}
				items={['first', 'second', 'dthghfghfhf']}
				headParams={{
					title: 'Скин:',
					icon: <TinyIcons.Arrow />
				}}
			/>
			<Row gap='3rem'>
				<UIKIT.DropDown
					onChange={setFoo}
					items={['first', 'second', 'dthghfghfhf']}
					headParams={{
						title: 'Цена:',
						icon: <TinyIcons.Arrow />
					}}
				/>
				<UIKIT.DropDown
					onChange={setFoo}
					items={['first', 'second', 'dthghfghfhf']}
					headParams={{
						title: 'Редкость:',
						icon: <TinyIcons.Arrow />
					}}
				/>
			</Row>
		</Filters>
	)
}
