import { TinyIcons } from "assets/tiny-icons";
import Players from "components/Players";
import Row from "components/Row";
import ActionButton from "components/UI/ActionButton";
import { IServer } from "interfaces/Server";
import { Card, Ip, Map, Name } from "./Styles";

export function RowServerCard({ server_name, map, max_players, players, ip, port, ...props }: IServer) {
	return (
		<Card {...props}>
			<Name onClick={e => e.stopPropagation()}>{server_name}</Name>
			<Map onClick={e => e.stopPropagation()}>{map}</Map>
			<Players max_players={max_players} players={players} />
			<Ip onClick={e => e.stopPropagation()}>{ip}:{port}</Ip>
			<Row onClick={e => e.stopPropagation()}>
				<ActionButton>
					<TinyIcons.Play />
				</ActionButton>
				<ActionButton>
					<TinyIcons.Copy />
				</ActionButton>
				<ActionButton>
					<TinyIcons.Shiled />
				</ActionButton>
			</Row>
		</Card>
	)
}
