import KeyCard from 'components/Cards/KeyCard'
import Column from 'components/Column'
import Row from 'components/Row'
import UIKIT from 'components/UI'
import { IKeyCard } from 'interfaces/KeyCard'
import React from 'react'
import { Container, Grid, Title, Wrapper } from './Styles'

export default function Credits() {

	const items: IKeyCard[] = [
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
		{
			price: 100,
			credit_price: '100.000',
			src: '/static/images/key.png'
		},
	]

	return (
		<Wrapper>
			<Column gap='3rem' style={{ alignSelf: 'center' }}>
				<Title>
					Покупка кредитов на сайт
				</Title>
				<Container>
					<Row gap='3rem'>
						<UIKIT.Select
							params={{
								headHeight: '5rem',
								width: '100%',
								position: 'center'
							}}
							items={['']}
							onChange={() => { }}
						/>
						<UIKIT.Button
							color='blue'
							height='5rem'
							variant='default'
							style={{ padding: '1.5rem 6rem' }}
						>
							Купить
						</UIKIT.Button>
					</Row>
				</Container>
			</Column>
			<Title>
				Покупка кредитов на сервера
			</Title>
			<Grid>
				{items.map(({ src, price, credit_price }, index) =>
					<KeyCard
						key={index}
						src={src}
						price={price}
						credit_price={credit_price}
					/>
				)}
			</Grid>
		</Wrapper>
	)
}
