import { useEffect, useState } from 'react'
import Tabs from "components/Templates/Tabs";
import Layout from "../../components/Layout";
import { DashBoardContext } from 'context/dashboard';
import Servers from 'components/Servers';
import FooterTemplate from 'components/Templates/Information';
import FooterColumn from 'components/Templates/Information/Column';
import Column from 'components/Column'
import { TinyIcons } from 'assets/tiny-icons';
import LayoutIcon from 'components/LayoutIcon';
import { useAppSelector } from 'hooks/useAppSelector';
import { useAppDispatch } from 'hooks/useAppDispatch';
import { fetchServers } from 'redux/reducers/servers.slice';
import { useServers } from 'hooks/useServers';

export default function Dashboard() {

	const [tab, setTab] = useState(0)

	const { servers: rawServers } = useAppSelector(state => state.servers)
	const dispatch = useAppDispatch()

	const [layoutType, setLayoutType] = useState(0)

	useEffect(() => {
		dispatch(fetchServers())
	}, [])

	const categories = [...new Set(rawServers.map((server) => server.type))]
	const tabItems = ['Все', ...categories] as string[]

	const servers = useServers(rawServers, tabItems[tab])

	return (
		<DashBoardContext.Provider value={{ tab, setTab }}>
			<Layout
				headerElement={
					<Tabs
						type='default'
						items={tabItems}
						context={DashBoardContext}
					/>
				}
				leftElement={
					<Column gap='2rem'>
						<LayoutIcon className={layoutType === 0 ? 'active' : ''} onClick={() => setLayoutType(0)}>
							<TinyIcons.LayoutFirst />
						</LayoutIcon>
						<LayoutIcon className={layoutType === 1 ? 'active' : ''} onClick={() => setLayoutType(1)}>
							<TinyIcons.LayoutSecond />
						</LayoutIcon>
					</Column>
				}
				footerElement={
					<FooterTemplate>
						<FooterColumn items={['Общий онлайн:', '617']} />
						<FooterColumn items={['Игроков в сутки:', '2 014']} />
						<FooterColumn items={['Блокировок:', '26']} />
					</FooterTemplate>
				}
			>
				<Servers servers={servers} layout={layoutType} />
			</Layout>
		</DashBoardContext.Provider>
	)
}