import { SelectVariants } from "interfaces/Select";
import styled, { css } from "styled-components";

interface Props {
	height?: string;
	position?: 'left' | 'center' | 'right';
	variant?: SelectVariants
}

export const HeadBlock = styled.div<Props>`
	position: relative;
	z-index: 4;
	user-select: none;
	width: 100%;
	cursor: pointer;
	height: ${props => props.height ? props.height : '6rem'};
	border-radius: 2rem;
	background: ${props => props.theme.colors.white.white10};
	border: .1rem solid ${props => props.theme.stroke.stroke5};
	display: flex;
	align-items: center;
	${props => props.variant === 'transparent' && css`
		background: transparent;
		border: none;
		height: fit-content;
		padding: 0 !important;
	`}
	${props => props.position === 'left' && css`
		justify-content: flex-start;
	`}
	${props => props.position === 'center' && css`
		justify-content: center;
	`}
	${props => props.position === 'right' && css`
		justify-content: flex-end;
	`}
	gap: 1rem;
	padding: 2rem;
	svg {
		transition: all .3s;
		transform: rotate(180deg);
	}
	&.active {
		svg {
			transform: rotate(0deg);
		}
	};
`;

export const Text = styled.div`
	${props => props.theme.fonts.GothamMedium20};
	color: ${props => props.theme.colors.white.white100};
`