import { Button } from "components/UI/Button/Styles";
import { motion } from "framer-motion";
import { InputBlock as InputStyles } from "components/UI/Input/Styles";
import styled, { css } from "styled-components";

export const Container = styled(motion.form)`
	max-height: 61rem;
	max-width: 38.2rem;
	width: 100%;
	padding: 2rem;
	/* position: fixed; */
	background: transparent;
	border-radius: 2.5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 2rem;
	border: .3rem solid ${props => props.theme.stroke.stroke5};
	${props => props.theme.shadows.defaultShadow}
`

export const Header = styled.div`

`

export const Messages = styled.div`
	display: flex;
	flex-direction: column;
	padding-right: 2rem;
	gap: 2.5rem;
	max-height: calc(100% - 2rem);
`

export const InputBlock = styled.div`
	width: 100%;
	position: relative;
	align-self: flex-end;
	${InputStyles} {
		padding-right: 10rem;
	}
`

export const Buttons = styled.div`
	position: absolute;
	right: 2rem;
	top: 3rem;
	display: flex;
	align-items: center;
	gap: 1rem;
	${Button} {
		width: fit-content;
		height: fit-content;
		padding: .6rem;
	}
`

export const ChatButton = styled.button<{ variant: 'gray' | 'blue' }>`
	border-radius: 50%;
	padding: .6rem;
	height: 3rem;
	width: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: .3s all;
	${props => props.variant === 'gray' && css`
		background: ${props => props.theme.colors.white.white10};
	`}
	${props => props.variant === 'blue' && css`
		background: ${props => props.theme.colors.blue.accent};
		&:hover {
			background: ${props => props.theme.colors.blue.hover};
		}
		&:active {
			background: ${props => props.theme.colors.blue.active};
		}
	`}
`

export const EmojiWrapper = styled.div`
	position: absolute;
	left: 0;
	bottom: 6rem;
`