import styled from "styled-components";

export const DiscountTooltip = styled.div`
	width: 100%;
	height: fit-content;
	padding: 2rem 3rem;
	border-radius: 2rem;
	background: rgba(30, 145, 228, 0.3);
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 3rem;
`

export const Wrapper = styled.div`

`

export const TooltipItem = styled.div`
	width: fit-content;
	display: flex;
	align-items: center;
	gap: 1rem;
`

export const Text = styled.span`
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
`

export const Discount = styled.span`
	${props => props.theme.fonts.GothamBold32};
	color: ${props => props.theme.colors.white.white100};
`

export const Promocode = styled.div`
	display: flex;
	align-items: center;
	width: fit-content;
	white-space: nowrap;
	cursor: pointer;
	gap: 1rem;
	transition: .3s;
	${props => props.theme.fonts.GothamRegular16};
	color: ${props => props.theme.colors.green.accent};
	&:hover {
		color: ${props => props.theme.colors.green.hover};
	}
	&:active {
		color: ${props => props.theme.colors.green.pressed};
	}
`

export const Grid = styled.div`
	margin-top: 3rem;
	display: grid;
	gap: 3rem;
	grid-template-columns: repeat(auto-fill, minmax(37rem, 1fr));
`