import { ServerCard, RowServerCard } from "components/Cards";
import { Body, GridLayout, Head, HeadItem, RowLayout } from "./Styles";
import { IServer } from 'interfaces/Server'
import { Modals } from "components/Modal";
import { useState } from "react";
import { CurrentServerContext } from "context/currentServer";

interface Props {
	servers: IServer[],
	layout?: number
}

export default function Servers({ servers = [], layout }: Props) {

	const [modal, setModal] = useState(false)
	const [server, setServer] = useState<IServer>({})

	return (
		<CurrentServerContext.Provider value={server}>
			{layout === 0 ?
				<GridLayout count={servers.length}>
					{servers.map((item, index) =>
						<ServerCard
							map={item.map}
							max_players={item.max_players}
							players={item.players}
							type={item.type}
							key={index}
							onClick={() => {
								setServer(servers[index])
								setModal(true)
							}}
						/>
					)}
				</GridLayout> :
				<RowLayout>
					<Head>
						<HeadItem>Название сервера</HeadItem>
						<HeadItem>Карта</HeadItem>
						<HeadItem>Игроки</HeadItem>
						<HeadItem>Ip-Адрес</HeadItem>
						<HeadItem>Действия</HeadItem>
					</Head>
					<Body className='customScroll'>
						{servers.map((item, index) =>
							<RowServerCard
								key={index}
								server_name={item.server_name}
								map={item.map}
								players={item.players}
								max_players={item.max_players}
								ip={item.ip}
								port={item.port}
								onClick={() => {
									setServer(servers[index])
									setModal(true)
								}}
							/>
						)}
					</Body>
				</RowLayout>
			}
			<Modals.DashboardServer isActive={modal} setActive={setModal} />
		</CurrentServerContext.Provider>
	)
}
