import { SuperTinyIcons } from 'assets/super-tiny-icons';
import { TinyIcons } from 'assets/tiny-icons';
import Image from 'components/Image';
import LabelAvatar from 'components/Labels/LabelAvatar';
import LabelIcon from 'components/Labels/LabelIcon';
import Row from 'components/Row';
import ServerPlayers from 'components/ServerPlayers';
import ActionButton from 'components/UI/ActionButton';
import { CurrentServerContext } from 'context/currentServer';
import React, { useContext } from 'react'
import { useTheme } from 'styled-components';
import Modal from '../Modal'
import { Banner, Body, BodyItem, Container, Head, Item, ServerConnect, ServerInfoHead, ShortInfo, Stats, Team } from './Styles'

interface Props {
	isActive: boolean;
	setActive: React.SetStateAction<any>
}

export default function DashboardServer({ isActive, setActive }: Props) {

	const currentServer = useContext(CurrentServerContext)

	const Theme = useTheme()

	return (
		<Modal isActive={isActive} setActive={setActive}>
			<Container>
				<Head>
					<Banner>
						<div className="bg"></div>
						<Image
							src="/static/images/map.png"
							alt="server"
							className='map'
						/>
						<Team style={{ marginLeft: "2rem" }}>
							<Image src='/static/images/CT.png' alt='Team' />
							Спецназ
						</Team>
						<Stats>
							11:9
							<LabelIcon icon={<SuperTinyIcons.Clock />} params={{ gap: '.6rem' }}>
								13:29
							</LabelIcon>
						</Stats>
						<Team>
							<Image src='/static/images/T.png' alt='Team' />
							Террористы
						</Team>
					</Banner>
					<ShortInfo>
						<ServerPlayers players={currentServer.players || 0} max_players={currentServer.max_players || 0} />
						<Row gap='5.5rem'>
							<Row>
								<ActionButton>
									<TinyIcons.Play />
								</ActionButton>
								<ActionButton>
									<TinyIcons.Copy />
								</ActionButton>
								<ActionButton>
									<TinyIcons.Shiled />
								</ActionButton>
							</Row>
							<ServerConnect>
								IP: {currentServer.ip}:{currentServer.port}
							</ServerConnect>
						</Row>
					</ShortInfo>
					<ServerInfoHead>
						<Item>
							Игрок
						</Item>
						<Row gap='5rem'>
							<Item>Время</Item>
							<Item>K/D</Item>
							<Item>Счет</Item>
						</Row>
					</ServerInfoHead>
				</Head>
				<Body className='customScroll'>
					{currentServer.teams?.counter_terrorists.players.map((item, index) =>
						<BodyItem key={index}>
							<Item>
								<LabelAvatar params={{ avatarSize: 'small', font: Theme.fonts.GothamRegular16 }}>
									{item.nickname}
								</LabelAvatar>
							</Item>
							<Row gap='5rem'>
								<Item>{item.time_playing}</Item>
								<Item>{item.kill_deaths}</Item>
								<Item>{item.score}</Item>
							</Row>
						</BodyItem>
					)}
					{currentServer.teams?.terrorists.players.map((item, index) =>
						<BodyItem key={index}>
							<Item>
								<LabelAvatar params={{ avatarSize: 'small', font: Theme.fonts.GothamRegular16 }}>
									{item.nickname}
								</LabelAvatar>
							</Item>
							<Row gap='5rem'>
								<Item>{item.time_playing}</Item>
								<Item>{item.kill_deaths}</Item>
								<Item>{item.score}</Item>
							</Row>
						</BodyItem>
					)}
				</Body>
			</Container>
		</Modal>
	)
}