import { Button } from "components/UI/Button/Styles";
import styled from "styled-components";

export const Transfer = styled.div`
	${props => props.theme.shadows.defaultShadow};
	border-radius: 2rem;
	position: absolute;
	padding: 3rem;
	display: flex;
	right: -4rem;
	top: 6rem;
	flex-direction: column;
	gap: 2rem;
	height: fit-content;
	max-width: 36rem;
	width: 100%;
	background: ${props => props.theme.colors.white.white100};
	opacity: 0;
	z-index: -2;
	pointer-events: none;
	transition: all .3s;
	&.active {
		opacity: 1;
		z-index: 2;
		pointer-events: all;
	}
	${Button} {
			width: 100%;
	}
`