import { Button } from "./Styles";
import { IButton } from "interfaces/Button";

export default function Buttons({ children, height, variant, leftIcon, rightIcon, color, className, ...props }: IButton) {
	return (
		<Button
			variant={variant}
			height={height}
			color={color}
			className={className}
			fullWidth={props.fullWidth}
			{...props}
		>
			{leftIcon && leftIcon}
			{children}
			{rightIcon && rightIcon}
		</Button>
	)
}
