import { ISelect } from 'interfaces/Select'
import React, { useState } from 'react'
import Body from './Body'
import Head from './Head'
import { Label, SelectBlock, Trigger } from './Styles'

export default function Select({ params, items, onChange, label, defaultValue = '' }: ISelect) {

	const [show, setShow] = useState(false)
	const [headLabel, setHeadLabel] = useState<string>('')
	const [defVal, setDefVal] = useState<string>(defaultValue)

	function onItemClick(index: number) {
		setShow(false)

		const label = items[index]?.label ? items[index]?.label : items[index]
		const value = items[index]?.value ? items[index]?.value : items[index]

		setHeadLabel(label)
		setDefVal('')
		onChange(value)
	}

	return (
		<>
			{show &&
				<Trigger onClick={() => setShow(false)} className={show ? 'active' : ''} />
			}
			<Label width={params?.width}>
				{label}
				<SelectBlock>
					<Head
						position={params?.position}
						height={params?.headHeight}
						setShow={setShow}
						show={show}
						label={headLabel}
						defaultValue={defVal}
						variant={params?.variant}
					/>
					<Body
						show={show}
						items={items}
						onItemClick={onItemClick}
						variant={params?.variant}
					/>
				</SelectBlock>
			</Label>
		</>
	)
}
