import { ProfileContext } from 'context/profile'
import React, { useState } from 'react'
import { useParams } from 'react-router-dom'
import { isYou } from 'utils/IsYou'
import Friends from './Tabs/Friends'
import Inventory from './Tabs/Inventory'
import Settings from './Tabs/Settings'
import Stats from './Tabs/Stats'

export default function Profile() {

	const params = useParams()

	const [tab, setTab] = useState(0)

	return (
		<ProfileContext.Provider value={{ tab, setTab }}>
			{params.tab === undefined && <Stats />}
			{params.tab === 'inventory' && <Inventory />}
			{params.tab === 'friends' && <Friends />}
			{params.tab === 'settings' && isYou(params.steamid64) && <Settings />}
		</ProfileContext.Provider>
	)
}
