import Avatar from 'components/Avatar';
import { IFriend } from 'interfaces/Friend';
import React from 'react'
import { Link } from 'react-router-dom';
import { Container, Nickname } from './Styles'

export default function FriendCard({ avatar, name, steamid64, last_online, id }: IFriend) {
	return (
		<Link to={`/profile/${steamid64}`}>
			<Container>
				<Avatar size='small-6' src={avatar} />
				<Nickname>
					{name}
				</Nickname>
			</Container>
		</Link>
	)
}
