import React, { useContext } from 'react'
import Image from 'components/Image';
import { MarketplaceContext } from 'context/marketplace';
import { useTheme } from 'styled-components';
import { Bottom, BuyMode, Container, Info, Name, OnSellMode, Price, SellMode, SubName } from './Styles'
import Modal from '../Modal'
import UIKIT from 'components/UI'
import { LabelAvatar } from 'components/Labels';

interface Props {
	isActive: boolean;
	setActive: React.SetStateAction<any>;
	mode: number;
}

export default function MarketplaceWeapon({ isActive, setActive, mode }: Props) {

	const currentWeapon = useContext(MarketplaceContext)

	const Theme = useTheme()

	return (
		<Modal isActive={isActive} setActive={setActive}>
			<Container>
				<Image src={currentWeapon.src} alt='Gun' />
				<Bottom>
					<Info>
						<Name>{currentWeapon.name} | <SubName rarity={currentWeapon.rarity}>&nbsp;{currentWeapon.sub_name}</SubName></Name>
						<Price>
							{mode === 1 && 'от'} {currentWeapon.price} Kpедитов
						</Price>
					</Info>
					<LabelAvatar params={{ color: Theme.colors.black.black100, avatarSize: 'small' }}>
						{currentWeapon.owner?.nickname}
					</LabelAvatar>
					{mode === 0 &&
						<BuyMode>
							<UIKIT.Button height='5rem' variant='default' color='blue'>
								Купить
							</UIKIT.Button>
						</BuyMode>
					}
					{mode === 1 &&
						<SellMode>
							<UIKIT.Input
								label='Цена'
								params={{
									labelPosition: 'center',
									labelColor: Theme.colors.black.black100,
									inputColor: Theme.colors.black.black100
								}}
							/>
							<UIKIT.Button height='5rem' variant='default' color='red'>
								Продать
							</UIKIT.Button>
						</SellMode>
					}
					{mode === 2 &&
						<OnSellMode>
							<UIKIT.Input
								label='Цена'
								params={{
									labelPosition: 'center',
									labelColor: Theme.colors.black.black100,
									inputColor: Theme.colors.black.black100
								}}
							/>
							<UIKIT.Button height='5rem' variant='default' color='red' disabled>
								Сохранить
							</UIKIT.Button>
							<UIKIT.Button height='5rem' variant='default' color='red'>
								Снять с продажи
							</UIKIT.Button>
						</OnSellMode>
					}
				</Bottom>
			</Container>
		</Modal>
	)
}
