import Avatar from 'components/Avatar';
import Row from 'components/Row';
import { AvatarVariants } from 'interfaces/Avatar';
import { Nickname } from './Styles';

interface Props {
	src?: string;
	children: any;
	params: {
		color?: string;
		font?: any;
		avatarSize: AvatarVariants;
		gap?: string;
	}
}

export default function LabelAvatar({ children, src, params }: Props) {
	return (
		<Row gap={params.gap || '1rem'} justifyContent='flex-start'>
			<Avatar size={params?.avatarSize} />
			<Nickname color={params?.color} font={params?.font}>
				{children}
			</Nickname>
		</Row>
	)
}
