import React from 'react'
import { DropDownHead, Icon, SubTitle, Title } from './Styles'

interface Props {
	params: {
		title?: string;
		icon: React.ReactElement | string
	};
	label?: string;
	setBodyShow: React.SetStateAction<any>;
	show: boolean;
	defaultValue?: string;
}

export default function Head({ params, setBodyShow, show, label, defaultValue }: Props) {
	return (
		<DropDownHead onClick={() => setBodyShow(!show)} className={show ? 'active' : ''}>
			<Title>{params.title}</Title>
			{defaultValue ?
				<SubTitle>
					{defaultValue}
				</SubTitle> :
				<SubTitle>
					{label ? label : 'Не выбрано'}
				</SubTitle>
			}
			<Icon>{params.icon}</Icon>
		</DropDownHead>
	)
}
