import styled from "styled-components";

export const Card = styled.div`
	width: 100%;
	height: fit-content;
	cursor: pointer;
	padding: 1rem 3rem;
	border: .3rem solid ${props => props.theme.stroke.stroke5};
	border-radius: 1.5rem;
	display: grid;
	grid-template-columns: .8fr .3fr .3fr .4fr .1fr;
	grid-template-rows: minmax(5rem, 1fr);
	align-items: center;
`

export const Name = styled.span`
	transition: .5s all;
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
	position: relative;
	z-index: 20;
	cursor: default;
`

export const Map = styled.span`
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
	position: relative;
	z-index: 20;
	cursor: default;
`

export const Ip = styled.span`
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
	position: relative;
	z-index: 20;
	cursor: default;
`