import Image from 'components/Image'
import UIKIT from 'components/UI'
import { IKeyCard } from 'interfaces/KeyCard'
import React from 'react'
import { Container, CreditPrice, Price, Text, Wrapper } from './Styles'

export default function KeyCard({ credit_price, price, src = '/static/images/key.png' }: IKeyCard) {
	return (
		<Container>
			<Wrapper>
				<Image src={src} alt='key' />
				<Text>
					<CreditPrice>
						{credit_price}
					</CreditPrice>
					кредитов
				</Text>
			</Wrapper>
			<Price>
				{price} руб
			</Price>
			<UIKIT.Button height='5rem' variant='default' color='blue'>
				Купить
			</UIKIT.Button>
		</Container>
	)
}
