import styled from "styled-components";
import { Template } from 'components/Templates/Information/Styles'

export const Wrapper = styled.div`
	display: flex;
	flex-direction: column;
	gap: 3rem;
	${Template} {
		margin-left: 0;
		width: 100%;
	}
`

export const History = styled.div`
	border-radius: 2.5rem;
	position: relative;
	width: 100%;
	height: fit-content;
	padding: 3rem;
	border: .3rem solid ${props => props.theme.stroke.stroke5};
	${props => props.theme.shadows.defaultShadow};
	display: flex;
	flex-direction: column;
	gap: 3rem;
`
export const Head = styled.div`
	
`

export const HeadItem = styled.div`
	${props => props.theme.fonts.GothamBold20};
	color: ${props => props.theme.colors.white.white100};
`

export const Body = styled.div`
	display: flex;
	flex-direction: column;
	gap: 3rem;
`

export const Row = styled.div`
	display: grid;
	width: 100%;
	grid-template-columns: .2fr .4fr .4fr .2fr;
` 

export const BodyItem = styled.div`
	${props => props.theme.fonts.GothamRegular20};
	color: ${props => props.theme.colors.white.white100};
`

export const ServiceItem = styled(BodyItem)`
	color: ${props => props.theme.colors.blue.accent};
`