import UIKIT from 'components/UI'
import { IReplenishment } from 'interfaces/Replenishment'
import { useState } from 'react'
import { Condition, Container, Info, Text, Banner } from './Styles'

export default function ReplenishmentCard({ text, condition, banner }: IReplenishment) {

	const [amount, setAmount] = useState<number>(0)

	return (
		<Container>
			<Info>
				<Text>
					{text}
				</Text>
				<Condition>
					{condition}
				</Condition>
			</Info>
			<Banner src={banner} alt='banner' />
			<UIKIT.Input
				placeholder='Введите сумму'
				params={{ width: '100%', height: '5rem' }}
				value={amount === 0 ? '' : amount}
				inputMode='numeric'
				onChange={e => setAmount(Number(e.target.value))}
			/>
			<UIKIT.Button color='blue' variant='default' height='5rem' disabled={amount === 0}>
				Пополнить баланс
			</UIKIT.Button>
		</Container>
	)
}
