import { TinyIcons } from 'assets/tiny-icons'
import ReplenishmentCard from 'components/Cards/ReplenishmentCard'
import Layout from 'components/Layout'
import SubHeader from 'components/SubHeader'
import { IReplenishment } from 'interfaces/Replenishment'
import React from 'react'
import { Discount, DiscountTooltip, Grid, Promocode, Text, TooltipItem, Wrapper } from './Styles'

export default function AccountReplenishment() {

	const items: IReplenishment[] = [
		{
			text: 'VISA/MasterCard/QIWI',
			condition: '(Только РФ без комиссии)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'ЮMoney',
			condition: '(Только РФ от 15р)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'QIWI',
			condition: '(Только РФ от 15р)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'VISA',
			condition: '(Только иностранцы)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'Оплата скинами/картой',
			condition: '(Не РФ от 75р)',
			banner: '/static/images/payment.png'
		},
	]

	return (
		<Layout
			leftElement={<div />}
			headerElement={
				<SubHeader>
					Пополнение баланса
				</SubHeader>
			}
		>
			<Wrapper>
				<DiscountTooltip>
					<TooltipItem>
						<Discount>+5%</Discount>
						<Text>При пополнении на сумму от 100 до 999руб</Text>
					</TooltipItem>
					<TooltipItem>
						<Discount>+15%</Discount>
						<Text>При пополнении на сумму от 1000 до 10000руб</Text>
					</TooltipItem>
					<Promocode>
						<TinyIcons.DiscountStar />
						Активировать промокод
					</Promocode>
				</DiscountTooltip>
				<Grid>
					{items.map(({ banner, text, condition }, index) =>
						<ReplenishmentCard
							key={index}
							banner={banner}
							text={text}
							condition={condition}
						/>
					)}
				</Grid>
			</Wrapper>
		</Layout>
	)
}
