import styled, { GradientKeys } from "styled-components";

interface Props {
	rarity: GradientKeys;
}

export const Container = styled.div<Props>`
	cursor: pointer;
	padding: 0 0 2rem;
	width: 18.7rem;
	height: fit-content;
	background: ${props => props.theme.gradients[props.rarity]};
	border-radius: 1.5rem;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
	gap: 1.6rem;
`

export const Info = styled.div`
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
	height: 100%;
	padding-left: 2rem;
`

export const Name = styled.div`
	${props => props.theme.fonts.GothamRegular14};
	color: ${props => props.theme.colors.white.white100};
	display: flex;
	font-weight: 400;
`

export const SubName = styled.div<Props>`
	font-weight: bold;
	${props => props.theme.fonts.GothamRegular14};
	color: ${props => props.theme.colors.weapon[props.rarity]};
`

export const Price = styled.div`
	${props => props.theme.fonts.GothamBold24};
	color: ${props => props.theme.colors.white.white100};	
`