import { Icons } from 'assets/icons'
import Column from 'components/Column'
import Layout from 'components/Layout'
import LayoutIcon from 'components/LayoutIcon'
import SubHeader from 'components/SubHeader'
import UIKIT from 'components/UI'
import { useState } from 'react'
import { getBansAndMutesTitle } from 'utils/getBansAndMutesTitle'
import { Body, BodyItem, Duration, Filters, Head, HeadItem, Reason, Server, Table, Wrapper } from './Styles'

export default function BansAndMutes() {

	const [mode, setMode] = useState(0)

	const [select, setSelect] = useState(null)

	const data = [
		{
			nickname: 'Den4is187',
			date: '16.11.2022, 17:25',
			server: 'Freezing-Blood | MIRAGE #3 | 128tick | AntiCheat',
			identifier: 'STEAM_1:0:566407427',
			duration: '24ч',
			reason: 'Auto B-hop'
		},
		{
			nickname: 'Valera Mohoboyka',
			date: '16.11.2022, 17:25',
			server: 'Freezing-Blood | MIRAGE #3 | 128tick | AntiCheat',
			identifier: 'STEAM_1:0:566407427',
			duration: 'Навсегда',
			reason: 'Slishkom krut'
		},
		{
			nickname: 'AlexUnder',
			date: '16.11.2022, 17:25',
			server: 'Freezing-Blood | MIRAGE #3 | 128tick | AntiCheat',
			identifier: 'STEAM_1:0:566407427',
			duration: '24ч',
			reason: 'AIM'
		},
	]

	return (
		<Layout
			headerElement={
				<SubHeader>
					{getBansAndMutesTitle(mode)}
				</SubHeader>
			}
			leftElement={
				<Column gap='2rem'>
					<LayoutIcon className={mode === 0 ? 'active' : ''} onClick={() => setMode(0)}>
						<Icons.BansLayout />
					</LayoutIcon>
					<LayoutIcon className={mode === 1 ? 'active' : ''} onClick={() => setMode(1)}>
						<Icons.Mutes />
					</LayoutIcon>
					<LayoutIcon className={mode === 2 ? 'active' : ''} onClick={() => setMode(2)}>
						<Icons.Applications />
					</LayoutIcon>
				</Column>
			}
		>
			<Wrapper>
				<Filters>
					<UIKIT.Select
						items={["Test 1", 'Test 2']}
						label={'Сервер'}
						params={{ width: '100%' }}
						onChange={setSelect}
					/>
					<UIKIT.Input
						label='Поиск'
						params={{ width: '100%' }}
					/>
				</Filters>
				<Table>
					<Head>
						<HeadItem>№</HeadItem>
						<HeadItem>Ник</HeadItem>
						<HeadItem>Дата</HeadItem>
						<HeadItem>Сервер</HeadItem>
						<HeadItem>Идентификатор</HeadItem>
						<HeadItem>Срок</HeadItem>
						<HeadItem>Причина</HeadItem>
					</Head>
					<Body>
						{data.map((item, index) =>
							<>
								<BodyItem>{index + 1}</BodyItem>
								<BodyItem>{item.nickname}</BodyItem>
								<BodyItem>{item.date}</BodyItem>
								<Server>{item.server}</Server>
								<BodyItem>{item.identifier}</BodyItem>
								<Duration>{item.duration}</Duration>
								<Reason>{item.reason}</Reason>
							</>
						)}
					</Body>
				</Table>
			</Wrapper>
		</Layout>
	)
}
