import API from 'API'
import { ProfileCard } from 'components/Cards'
import Tabs from 'components/Templates/Tabs'
import Button from 'components/UI/Button'
import { ProfileContext } from 'context/profile'
import { useActions } from 'hooks/useActions'
import { useAppSelector } from 'hooks/useAppSelector'
import { IUser } from 'interfaces/User'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { isYou } from 'utils/IsYou'
import { AdditionalButtons, TabsWrapper, Wrapper } from './Styles'

interface Props {
	children?: any;
	gap?: string;
}

export default function ProfileLayout({ children, gap }: Props) {

	const { tab, steamid64 } = useParams()
	const { user, anotherUser, personalFriends } = useAppSelector(state => state?.user)
	const { setAnotherUser, fetchFriends } = useActions()
	const [userData, setUserData] = useState<IUser>({} as IUser)
	const ISYOU = isYou(steamid64)
	const friendCondition = !personalFriends.find((friend) => friend.steamid64 === steamid64)

	useEffect(() => {
		if (steamid64 !== user?.user?.steamid64) {
			(async () => {
				const anotherUser = await API.UserService.getAnotherProfile(steamid64)
				setAnotherUser(anotherUser.data)
			})()
		}
	}, [steamid64])

	useEffect(() => {
		if (user?.user?.steamid64 !== steamid64) {
			setUserData(anotherUser)
			fetchFriends(userData?.user?.steamid64)
		} else {
			setUserData(user)
		}
	}, [user, anotherUser, steamid64])

	function handleTabs(setTab: (index: number) => void) {

		switch (tab) {
			case 'inventory':
				setTab(1)
				break;
			case 'friends':
				setTab(2)
				break;
			case 'settings':
				setTab(5)
				break;
			default:
				setTab(0)
				break
		}
	}

	const profileTabItems = ISYOU ? [
		{ href: `/profile/${userData?.user?.steamid64}`, label: 'Профиль' },
		{ href: `/profile/${userData?.user?.steamid64}/inventory`, label: 'Инвентарь' },
		{ href: `/profile/${userData?.user?.steamid64}/friends`, label: 'Друзья' },
		{ href: `/profile/${userData?.user?.steamid64}/masseges`, label: 'Сообщения' },
		{ href: `/profile/${userData?.user?.steamid64}/masseges`, label: 'Уведомления' },
		{ href: `/profile/${userData?.user?.steamid64}/settings`, label: 'Настройки' },
		{ href: `/profile/${userData?.user?.steamid64}/punishments`, label: 'Наказания' },
	] : [
		{ href: `/profile/${userData?.user?.steamid64}`, label: 'Профиль' },
		{ href: `/profile/${userData?.user?.steamid64}/inventory`, label: 'Инвентарь' },
		{ href: `/profile/${userData?.user?.steamid64}/friends`, label: 'Друзья' },
	]

	return (
		<Wrapper gap={gap}>
			<ProfileCard user={userData} />
			{!ISYOU &&
				<AdditionalButtons>
					<Button
						height='5rem'
						variant='default'
						color='blue'
					>
						Сообщение
					</Button>
					<Button
						height='5rem'
						variant='default'
						color='blue'
					>
						Обмен</Button>
					<Button
						height='5rem'
						variant='default'
						color='green' 
						disabled={friendCondition}
					>
						Добавить в друзья
					</Button>
					<Button
						height='5rem'
						variant='default'
						color='red'
					>
						Пожаловаться
					</Button>
					<Button
						height='5rem'
						variant='default'
						color='red'
					>
						Заблокировать
					</Button>
				</AdditionalButtons>
			}
			<TabsWrapper isYou={ISYOU}>
				<Tabs
					callback={handleTabs}
					type='link'
					context={ProfileContext}
					items={profileTabItems}
				/>
			</TabsWrapper>
			{children}
		</Wrapper>
	)
}
