import Image from "components/Image";
import { AvatarVariants } from "interfaces/Avatar";
import { Wrapper } from "./Styles";

interface Props {
	src?: string;
	alt?: string;
	size: AvatarVariants
}

export default function Avatar({ src = '/static/images/avatar.png', alt = 'Avatar', size }: Props) {
	return (
		<Wrapper size={size}>
			<Image src={src} alt={alt} />
		</Wrapper>
	)
}
