import { Icons } from "assets/icons";
import { useEffect } from 'react'
import Button from "../../UI/Button";
import { Auth, AvatarWrapper, Balance, Card, Highlighted, NonAuth } from "./Styles";
import Avatar from "components/Avatar";
import { NavLink, useLocation, useNavigate } from "react-router-dom";
import { useAppSelector } from "hooks/useAppSelector";
import { endpoints, url } from "API";
import { useAppDispatch } from "hooks/useAppDispatch";
import { login } from "redux/reducers/user.slice";

export default function HeaderUserCard() {

	const { isAuth, user } = useAppSelector(state => state.user)

	const location = useLocation()
	const navigate = useNavigate()
	const dispatch = useAppDispatch()

	function loginHandler() {
		window.location.href = url + endpoints.auth.login
	}

	useEffect(() => {
		dispatch(login())
		if (location.search !== "") {
			navigate("/")
			location.search = ""
		}
	}, [])

	return (
		<Card>
			{isAuth ?
				<Auth>
					{/* <IconsWrapper>
						<a href="javascript:void()">
							<Icons.VK />
						</a>
						<a href="javascript:void()">
							<Icons.Discord />
						</a>
					</IconsWrapper> */}
					<Balance>
						{user.balance.real}&nbsp;<Highlighted>Р</Highlighted>&nbsp;|&nbsp;{user.balance.virtual}&nbsp;<Highlighted>Кр</Highlighted>
					</Balance>
					<AvatarWrapper>
						<NavLink to='/account-replenishment'>
							<Icons.Wallet />
						</NavLink>
						<Icons.Reload />
						<Icons.Notification />
						<NavLink to={`/profile/${user.user.steamid64}`}>
							<Avatar size='small' src={user.user.avatar} />
						</NavLink>
					</AvatarWrapper>
				</Auth> :
				<NonAuth>
					{/* <IconsWrapper>
						<a href="javascript:void()">
							<Icons.VK />
						</a>
						<a href="javascript:void()">
							<Icons.Discord />
						</a>
					</IconsWrapper> */}
					<Button
						height='5rem'
						variant='default'
						color='blue'
						rightIcon={<Icons.Steam />}
						onClick={loginHandler}
					>
						Войти
					</Button>
				</NonAuth>
			}
		</Card>
	)
}